















































import { Component, Prop, Vue, Model } from "vue-property-decorator";

@Component({
  components: {}
})
export default class Siderbar extends Vue {
  @Model("change", { type: Boolean }) collapsed!: boolean;

  /**
   *
   * @desc   切换菜单栏效果
   */
  toggleCollapsed(): void {
    this.collapsed = !this.collapsed;
  }
}
